    top: 16%;
    font-size: 20px;
}

.headerTitleLg {
    position: absolute;
    top: 18%;
    font-size: 100px;
    color: rgb(4, 29, 51);
}

.headerImg {
    width: 100%;
    height: 450px;
    margin-top: 80px;
    object-fit: cover;
}